/* Right sidebar */
#right-sidebar-outer {
    display: none;
}

body.home #right-sidebar-outer {
    display: block;
    padding: 0;
    position: fixed;
    height: 100%;
    right: 0;
    z-index: 8;
    top: 0;
    width: 10%;
    text-align: center;
    #right-sidebar {
        position: absolute;
        height: 100%;
        width: 100%;
        .primary-sidebar{
            height: inherit;
            div {
                height: inherit;
                #menu-right-menu {
                    height: 100%;
                    padding: 0;
                    margin: 0;
                    list-style-type: none;
                    li {
                        position: absolute;
                        transform: translate(-50%,-30px);
                        left: 50%;
                        border: 0;
                        font-family: $headingText;
                        font-size: 20px;
                        &:nth-of-type(1) {
                            transform: translate(0%,30px)  rotate(90deg) ;
                            transform-origin: 0%;
                            &:after {
                                content: '';
                                width: 20px;
                                border-bottom: 3px solid white;
                                display: inline-block;
                                margin-bottom: -10px;
                            }
                        }
                        &:nth-of-type(2) {
                            bottom: 0;
                            width: 100%;
                            &:before {
                                content: '';
                                width: 20px;
                                border-bottom: 3px solid white;
                                display: inline-block;
                                margin-bottom: -10px;
                            }
                        }
                    }
                    a,a:active,a:hover {
                        color: white;
                    }
                }
            }
        }
    }
}
