/* Main navigation */
#main-nav-outer {
    position: absolute;
    height: 100%;
    width: 90%;
    background: lighten($grey, 75%);
    z-index: 9;
    display: none;
    overflow: hidden;
    margin-left: 10%;
    min-height: 100%;
    nav {
        position: relative;
        display: none;
        margin: 0 auto;
        ul {
            list-style-type: none;
            padding: 0;
            li {
                font-size: 30px;
                margin: 5px 0;
                text-align: center;
                text-align: left;
                clear: left;
                a,a:active {
                    color: lighten($grey, 25%);
                    background: white;
                    padding: 2% 2% 2% 0;
                    margin: 5px 0;
                    transition: all .8s;
                    position: relative;
                    left: -300px;
                    display: inline-block;
                    width: 0%;
                    text-align: right;
                }
            }
        }
    }
    #search {
        background: white;
        img {
            width: 200px;
            margin: 0 auto;
            margin-top: 165%;
            display: block;
            transition: all ease-in;
            opacity: 0;
        }
        .widget-area {
            form {
                padding: 20px;
                input {
                    width: 100%;
                    border-radius: 20px;
                    border: 1px solid $grey;
                    padding: 10px;
                    background: white;
                    box-sizing : border-box;
                }
                button {
                    display: none;
                }
            }
        }
    }
}
#main-nav-outer.nav-slide {
    margin-top: 0px;
}