/**
* 1. Main
*/
html {
    height: 100%;
    body {
        background: url(http://silvestervella.com/newcasinosite/wp-content/uploads/2018/05/background-tile-1.png);
        color: $grey;
        font-family: $mainText;
        font-size: 16px;
        height: 100%;
    }
}
#wrapper {
    padding: 0;
    height: 100%;
    position: relative;
    margin-left: 10%;
    main{
        min-height: 100%;
        position: relative;
        display: block;
        margin: 0 auto;
        text-align: center;
    }
}
p {
    font-family: $mainText;
    text-align: justify;
    margin: 0 0 20px 0;
}
h1,h2,h3,h4,h5,h6 {
    font-family: $headingText;
    font-weight: 400;
    margin: 0;
}
button {
    background: $primary;
    border: 0;
    font-family: $headingText;
    @include no-outline;
}
input , input:hover , input:active , input:focus , select , select:active , select:focus , textarea {
    @include no-outline;
}
a, a:active,a:focus,a:hover {
    text-decoration: none;
    text-transform: uppercase;
}
.main-wrap {
    display: inline-block;
}
.post-outer {
    opacity: 0;
    transition: all 1.5s;
    width: 69%;
    position: relative;
    margin: 0 auto;
    padding: 0 15px;
    display: block;
    text-align: left;
    float: left;
}
.post-sidebar {
    position: relative;
    opacity: 0;
    transition: all 1.5s;
    transform: translateY(-50px);
    width: 19%;
    display: block;
    margin: 0 auto;
    text-align: left;
    float: left;
}
.post-head {
    height: 400px;
    position: relative;
    .feat-img {
        height: 100%;
        width: 75%;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
    }
    h1 {
        position: absolute;
        bottom: 50%;
        right: 25%;
        opacity: 0;
        transform: translate(70%, 50%);
        @include main-title;
        font-weight: 400;
        transition: all 1.5s;
        background: rgba( lighten($grey, 75%), .8 );
        &:after {
            content: '';
            width: 50px;
            border-bottom: 3px solid lighten($grey, 20%);
            display: inline-block;
            margin-bottom: -20px;
        }
    }
}
article.post , article.page {
    font-size: 20px;
    .post-content {
        padding: 100px 15px 0 15px;
    }
    h3 {
        margin: 100px 0 30px 0;
    }
}
.post-content:after {
    content: '';
    width: 50px;
    border-bottom: 3px solid #666;
    display: inline-block;
    padding: 50px 0 15px 0;
}



/**
* 2. Home Page
*/
main#home {
        overflow: hidden;
    }
.home-articles {
    position: absolute;
    height: 100%;
    width: 100%;
    display: none;
    background-repeat: no-repeat;
    background-size: cover;
    font-size: 20px;
    .home-title {
        padding-top: 200px;
    }
    .left {
        width: 50%;
        height: 100%;
        float: left;
        padding: 30px;
        text-align: left;
        box-sizing : border-box;
        h3 {
            position: absolute;
            left: 40%;
            transform: translate(-10%, 0px);
            top: 150px;
            font-size: 90px;
            color: lighten($grey, 10%);
            opacity: 0;
            font-weight: 400;
            transition: opacity 1.5s;
            span {
                background: white;
                display: inline-block;
                padding: 20px 50px;
                text-transform: uppercase;
                background: url(http://silvestervella.com/newcasinosite/wp-content/uploads/2018/05/background-tile-1.png);
            }
            span:nth-of-type(2) {
                font-size: 50px;
            }
        }
        .article-excerpt {
            position: absolute;
            top: 50%;
            width: 35%;
            transform: translate(0,-50%);
            font-family: $mainText;
            opacity: 0;
            transition: all 1.5s;
            box-sizing : border-box;
            .excerpt-text {
                background: url(http://silvestervella.com/newcasinosite/wp-content/uploads/2018/05/background-tile-1.png);
            }
            .read-more {
                margin-top: 50px;
                a {
                    @include main-button;
                }
            }
        }
    }
    .right {
        width: 50%;
        height: 100%;
        float: left;
        padding: 30px;
        text-align: left;
        box-sizing : border-box;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
    }
}
#home-pagination {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-100%,-30px);
    transform-origin: 0% 50%;
    .buttons {
        float: left;
        margin-top: 28px;
        display: none;
        button {
            width: 50px;
            height: 50px;
            border-radius: 50%;
            border: 1px solid lighten($grey, 50%);
            margin-right: 30px;
            font-size: 25px;
            color: lighten($grey, 50%);
        }
    }
    .scroll {
        float: left;
        margin-right: 30px;
        transform: translateY(0px);
        animation-name: scroll-outer;
        animation-duration: 2s;
        animation-iteration-count: infinite;
        animation-timing-function: ease-in-out;
        svg {
            width: 45px;
            position: relative;
            #mouse {
                opacity: 1;
                animation-name: scroll-opac;
                animation-duration: 2s;
                animation-iteration-count: infinite;
            }
            #wheel {
                transform: translateY(0px);
                animation-name: scroll-wheel;
                animation-duration: 1s;
                animation-iteration-count: infinite;
                animation-timing-function: ease-in-out;
            }
        }
    }
}



/**
* 3. Games Post
*/
body.post-template-template-single-games {
    .post-outer {
        width: 80%;
    }
    .mob-filter {
        display: none;
        background: $primary;
        width: 100%;
        span {
            width: 33.33%;
            display: inline-block;
            padding: 15px;
            text-align: center;
            color: white;
            border-left: 1px solid white;
            &:nth-of-type(1) {
                border: 0;
            }
        }
}
}
#games {
    .game-single {
        height: 300px;
        width: 31%;
        margin: 15px;
        overflow: hidden;
        position: relative;
        float: left;
        box-sizing : border-box;
    }
}
        .game-info {
            position: absolute;
            height: 100%;
            width: 100%;
            top: 0;
            left: 0;
            transform: translateY(300px);
            transition: all 1.5s;
            background: rgba( lighten($grey, 75%), .85 );
            padding: 50px 15px 15px 15px;
            box-sizing : border-box;
            .game-actions {
                position: absolute;
                bottom: 0;
                left: 0;
                right: 0;
                padding: 15px;
                box-sizing : border-box;
                text-align: center;
                 .game-demo {
                     a {
                        @include main-button;
                        display: inline-block;
                     }
                }
                .game-media {
                    a {
                        background: lighten($grey, 20%);
                        padding: 10px 20px;
                        border-radius: 100px;
                        color: white;
                        display: inline-block;
                        margin: 5px;
                    }
                }
            }
        }
        .game-title {
            @include item-title;
            font-size: 20px;
        }


    #filter {
        margin: -50px 15px 0 15px;
        padding: 0 15px 30px 15px;
        background: $primary;
        span {
            @include side-title;
            color: white;
        }
        select {
            display: block;
            width: 100%;
            border: 0;
            padding: 10px;
            background: white;
            outline: 0px;
        }
        button {
            @include main-button;
            background: white;
            color: $primary;
            margin-top: 30px;
        }
    }
    #latest-games {
        margin-top: 50px;
        span {
            @include side-title;
            &:after {
                content: '';
                width: 35px;
                border-bottom: 2px solid lighten($grey, 20%);
                display: inline-block;
                margin-bottom: -10px;
            }
        }
        .fl-game {
            height: 200px;
            margin: 15px;
            overflow: hidden;
            position: relative;
            .game-info {
                transform: translate(150px , 0px);
                padding: 15px;
                .game-actions {
                    padding: 15px 0;
                    text-align: center;
                    text-align: right;
                    .game-demo {
                        margin: 5px;
                        display: block;
                        text-align: right;
                    }
                }

            }
        }
    }


/*
* 4. News Page
*/
body.post-template-template-main-news {
    .post-outer {
        float: unset;
    }
    #all-news {
        margin-top: -50px;
        .news-outer {
            background: lighten($grey, 75%);
            margin-bottom: 30px;
            padding: 30px;
            .newspage-newstitle {
                @include item-title;
            }
            .newspage-news {
                padding-bottom: 30px;
                border-bottom: 1px solid lighten($grey, 60%);
            }
            .read-more {
                a {
                    @include main-button;
                    margin-top: 20px;
                }
            }
        }
    }
}

/**
* 5. About Page 
*/
body.post-template-template-single-about {
    .post-outer {
        float: unset;
        h1 {
            padding-left: 15px;
        }
        .post-content {
            padding-top: 15px;
        }
    }
    svg {
        position: relative;
        top: 0;
        left: 0;
        right: 0;
        width: 80%;
        margin: 50px auto 0 auto;
        #imgs {
            width: 250px;
            opacity: 0;
            transform: translate(0px, 5%);
            transition: all 1.5s;
        }
        #circle {
            opacity: 0;
            transition: all 1.5s;
        }
        #left-box , #striped-box {
            opacity: 0;
            transform: translate(10%, 0px);
            transition: all 1.5s;
        }
        #right-box {
            opacity: 0;
            transform: translate(-10%, 0px);
            transition: all 1.5s;
        }
    }
    article {
        .post-content {
            opacity: 0;
            transition: all 1.5s;
            padding-top: 50px;
        }
    }
}

/** 
* 6. Contact Page 
*/

body.page-template-template-single-contact {
    .post-outer {
        padding-top: 50px;
        width: 69%;
        h3 {
            padding: 0 0 15px 15px;
        }
        .post-content {
            padding-top: 0;
            margin-bottom: 50px;
        }
    }
    .post-sidebar {
        padding: 50px 15px 0 15px;
        h3 {
            padding: 0 0 15px 15px;
        }
    }
    form#main-form , form#newsletter-form {
        background: lighten($grey, 75%);
        padding: 20px;
        margin: 0 15px;
        .form-group {
            max-width: 600px;
            margin: 0 auto;
            margin-bottom: 30px;
        }
        label {
            font-family: $headingText;
            font-size: 18px;
            font-weight: 400;
            color: $primary;
            display: block;
        }
        button {
            @include main-button;
            margin-top: 30px;
        }
        input , input:focus  {
            @include no-outline;
            border-bottom: 1px solid lighten($grey, 45%);
            width: 100%;
            padding: 15px;
        }
        textarea , textarea:focus {
            @include no-outline;
            border-bottom: 1px solid lighten($grey, 45%);
            width: 100%;
            padding: 15px;
        }
        .form-check {
            input {
                width: auto;
            }
        }
    }
    form#main-form {
        padding-top: 50px;
    }
}
/**
* Client area page 
*/
body.page-template-template-single-clients {
    .post-outer {
        float: unset;
    }
}



/* Generic classes */
.opacTrans-anim {
    opacity: 1 !important;
    transform: translate(0px, 0px) !important;
}
.opacTrans-anim2 {
    opacity: 1 !important;
    transform: translate(0px, -50%) !important;
}
.opac-anim {
    opacity: 1 !important;
}
.pageTitle-anim {
    opacity: 1 !important;
    transform: translate(50%, 50%) !important;
}
.visible-article {
    display: block !important;
}